<template>
	<view class="h5-newInformation">
		<view class="zw"></view>
		<view class="news-title title">
			<view class="left"  @tap.stop="backs" >
				<image class="img"src="/static/image/back.png"></image>
			</view>
			<view class="field">编辑</view>
		</view>
		<view class="zwPosition"></view>
		<view class="contents" >
				<form class="sale-form" @submit="formSubmit">
					<view class="listBox noEdit">
						<view class="field  noWrite">房屋种类</view>
						<view class="value-box  noValueBox">毛坯房</view>
						<view class="right"></view>
					</view>
					<view class="listBox noEdit">
						<view class="field  noWrite">所在城市</view>
						<view class="value-box  noValueBox">杭州</view>
						<view class="right"></view>
					</view>
					<view class="listBox noEdit">
						<view class="field  noWrite">所在小区</view>
						<view class="value-box  noValueBox">晨光国际</view>
						<view class="right"></view>
					</view>
					<view class="listBox noEdit">
						<view class="field  noWrite">具体地址</view>
						<view class="value-box  noValueBox">1栋2094</view>
						<view class="right"></view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">如何称呼</view>
						<input class="value-box  canValueBox" name="name" v-model="name"  />
						<view class="right"></view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">联系方式</view>
						<input class="value-box  canValueBox" name="phone" v-model="phone"  />
						<view class="right"></view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">租金</view>
						<input class="value-box  canValueBox" name="phone" v-model="rentMoney"  />
						<view class="right">元</view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">租期</view>
						<input class="value-box  canValueBox" name="phone" v-model="rentTime"  />
						<view class="right"></view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">付款方式</view>
						<picker   class=" value-box  canValueBox" @change="pickerChange($event,1)" :value="index" :range="payWayList">
								<input class="uni-input"  :disabled="true"  name="payWay" v-model="payWay"/>
						</picker>
						<view class="right">
							 <image class="arrow" src="../../../static/image/right-gray.png"></image>
						</view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">是否合租</view>
						<picker   class=" value-box  canValueBox" @change="pickerChange($event,2)" :value="index" :range="joinTeamList">
								<input class="uni-input"  :disabled="true"  name="joinTeam" v-model="joinTeam"/>
						</picker>
						<view class="right">
							 <image class="arrow" src="../../../static/image/right-gray.png"></image>
						</view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">面积</view>
						<input class="value-box  canValueBox" name="area" v-model="area"  />
						<view class="right"></view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">朝向</view>
						<picker   class=" value-box  canValueBox" @change="pickerChange($event,3)" :value="index" :range="direcitonList">
								<input class="uni-input"  :disabled="true"  name="direction" v-model="direction"/>
						</picker>
						<view class="right">
							 <image class="arrow" src="../../../static/image/right-gray.png"></image>
						</view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">户型</view>
						<input class="value-box  canValueBox" name="houseStyle" v-model="houseStyle"  />
						<view class="right"></view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">楼层</view>
						<input class="value-box  canValueBox" name="houseStyle" v-model="houseFloor"  />
						<view class="right"></view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">入住</view>
						<picker   class=" value-box  canValueBox" @change="pickerChange($event,4)" :value="index" :range="checkList">
								<input class="uni-input"  :disabled="true"  name="check" v-model="check"/>
						</picker>
						<view class="right">
							 <image class="arrow" src="../../../static/image/right-gray.png"></image>
						</view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">装修情况</view>
						<input class="value-box  canValueBox" name="decorateHouse" v-model="decorateHouse"  />
						<view class="right"></view>
					</view>
					<view class="listBox noEdit">
						<view class="field  canWrite">水电情况</view>
						<picker   class=" value-box  canValueBox" @change="pickerChange($event,5)" :value="index" :range="waterElectriList">
								<input class="uni-input"  :disabled="true"  name="waterElectri" v-model="waterElectri"/>
						</picker>
						<view class="right">
							 <image class="arrow" src="../../../static/image/right-gray.png"></image>
						</view>
					</view>
					<view class="textarea-box">
						<view class="title">家具家、电</view>
						<textarea class="textarea" name="allElecFurnitrue" v-model="allElecFurnitrue"></textarea>
					</view>
					<view class="textarea-box">
						<view class="title">配套设施</view>
						<textarea class="textarea" name="configurateFacilite" v-model="configurateFacilite"></textarea>
					</view>
					<view class="photo-box">
						<view class="title">上传房产照片<text  class="tips">(最多可上传6张)  </text> </view> 
                         <view class="showPhoto-box">
							 <view class="photoLists"  v-for="(item,index) in photoList"  :key="index">
							 	<image  :src="item" class="uplpadImg"   @tap.stop="previewImage(index)"></image>
							 	<image  class="del" @tap.stop="delImg(index)" src="../../../static/image/uploadDel.png"></image>
							 </view>
							 <view class="box"  @tap.stop="getImage" v-if="photoList.length>=0 && photoList.length<6" >
								<image class="photo-bg" src="../../../static/image/uploadPhoto.png"></image>
							 </view>
							
						 </view>
					</view>
					<button class="btn" form-type="submit">提交</button>
					<view class="btns"></view>
			</form>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				// 称呼
				name:'王亚妮',
				phone:'15255132094',
				rentMoney:12,
				rentTime:'12个月',
				payWay:'押一付三',
				payWayList:['押一付三','季付','年付','押一付一'],
				joinTeamList:['是','否'],
				joinTeam:'是',
				area:'50㎡',
				direction:'东',
				direcitonList:['东','南','西','北'],
				houseStyle:'两室一厅',
				houseFloor:15,
				// 什么时候入住？
				check:'随时',
				checkList:['随时','一个月后','半年后'],
				decorateHouse:'精装修',
				waterElectri:'自费',
				waterElectriList:['自费','商用','民用','免费'],
				allElecFurnitrue:'冰箱、电话、电视、洗衣机、空调',
				configurateFacilite:'电梯、车库、天然气',
				photoList:[]
				
			}
		},
		onReachBottom(){
			
		},
		onLoad(options){
			this.setData(options);
		},
		methods: {
			backs(){
				uni.navigateBack(
					
				)
			},
			formSubmit(e){
				console.log(e.detail.value)
			},
			pickerChange(e,type){
				if(type ==1){
					this.payWay=this.payWayList[e.target.value];
				}
				else if(type ==2){
					this.joinTeam=this.joinTeamList[e.target.value];
				}
				else if(type ==3){
					this.direction=this.direcitonList[e.target.value];
				}else if(type ==4){
					this.check=this.checkList[e.target.value];
				}else if(type ==5){
					this.waterElectri=this.waterElectriList[e.target.value];
				}
			},
			getImage(){
				let _that = this;
				let lls = _that.photoList;
				uni.chooseImage({
					count: 6 - lls.length, //上传图片的数量，默认是9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album', 'camera'], //从相册选择
					success: function(res) {
						const tempFilePaths = res.tempFilePaths; //拿到选择的图片，是一个数组
						_that.photoList = lls.concat(res.tempFilePaths)
						
						// console.log(235453)
				// 		tempFilePaths.map(sos => {
				// 			uni.uploadFile({
				// 				url: 'http://zxyptest.hzbixin.cn/file/upload',
				// 				filePath: sos,
				// 				name: 'file',
				// 				success: function(datas) {
				// 					let results = typeof datas.data === "object" ? datas.data : JSON.parse(datas.data);
				// 					let aa = results.result;
				// 					_that.upUrlList.push(aa)
				
				// 				},
				// 				fail: function(datas) {}
				// 			})
				// 		})
					}
				});
			},
			// 图片预览
			previewImage(index) {
				uni.previewImage({
					current: this.photoList[index],
					urls: this.photoList,
					indicator: "number"
				})
			},
			//删除某一项
			delImg(index) {
				let arr = this.photoList
				arr.splice(index, 1)
				this.photoList = arr;
				// 上传的图片url
				// this.upUrlList.splice(index, 1)
			},
		}
	}
</script>

<style scoped lang="scss">
	@import "../../../static/scss/common.scss";
	// 字体
	// @import "../../../static/scss/font.css";
	.zw{
		height: var(--status-bar-height);
		width: 750rpx;
		
		position: fixed;
		top:0rpx;
		left:0rpx;
		background-color: white;
		z-index:20;
	}
	.zwPosition{
		width: 750rpx;
		height: 75rpx;
		margin-top: var(--status-bar-height);
	}
	.news-title{
		
		 @extend  %title;
		 border-bottom:2rpx solid #eee;
		 .left{
			 width:60rpx;
			 height: 75rpx;
			 line-height: 75rpx; 
			 position: absolute;
			 left:30rpx;
			 top:7.5rpx;
		 }
		 .img{
		 	display: block;
		 	width:19rpx;
		 	height: 30rpx;
			margin-top:23rpx;
		 }
		 .field{
			 @extend  %title-field;
		 }
	}
	.contents{
		// position: relative;
		// left:0rpx;
		// top:0rpx;
		 .listBox{
			 width: 750rpx;
			 height: 98rpx;
			 display: flex;
			 align-items: center;
			 padding:0rpx 30rpx;
			 box-sizing: border-box;
			 font-size: 28rpx;
			 justify-content: space-between;
			 border-bottom:2rpx solid #f2f2f2;
			 .noWrite{
				 color: #B3B3B3;
			 }
			 .field{
				 font-weight: bold;
				 width:180rpx;
				 
			 }
			 .canWrite{
				 color: #303133;
			 }
			 .noValueBox{
				  color: #B3B3B3;
			 }
			 .value-box{
				  width:480rpx;
			 }
			 .canValueBox{
				 color: #303133;
			 }
			 .right{
				  width: 60rpx;
				  height: 60rpx;
				  // background-color: red;
				  display: flex;
				  justify-content: flex-end;
				  align-items: center;
				  
				  .arrow{
					  display: block;
					  width: 23rpx;
					  height: 36rpx;
					
				  }
			 }
			 
		 }
		
	}
	.textarea-box{
		width: 750rpx;
		padding:30rpx;
		box-sizing: border-box;
		.title{
			color: #303133;
			font-size: 28rpx;;
		}
		.textarea{
			width: 690rpx;
			height: 200rpx;
			border: 2rpx solid #C9C9C9;
			border-radius: 4rpx;
			margin-top:36rpx;
			color: #222222;
			font-size: 28rpx;
			padding: 20rpx;
			box-sizing: border-box;
		}
		
	}
	.photo-box{
		width: 750rpx;
		padding:30rpx;
		box-sizing: border-box;
		font-size: 28rpx;
		.title{
			color: #303133;
			font-weight: bold;
			margin-bottom:40rpx;
			.tips{
			   color:#888888;
			}
		}
		.showPhoto-box{
			 display:flex;
			 flex-wrap: wrap;
			.box{
				.photo-bg{
					display: block;
					width: 157rpx;
					height: 157rpx;
				}
			}
			.photoLists{
				margin: 0rpx 20rpx 20rpx 0rpx;
				
				position: relative;
				left:0rpx;
				top:0rpx;
				.uplpadImg{
					display: block;
					width: 157rpx;
					height: 157rpx;
					border-radius: 4rpx;
				}
				.del{
				   display: block;
				   width: 34rpx;
				   height: 34rpx;
				   position: absolute;
				   right:-17rpx;
				   top:-17rpx;
				}
			}
			
		}
	}
  .btn{
	  width: 750rpx;
	  height: 98rpx;
	  line-height: 98rpx;
	  text-align: center;
	  background-color: #4270ED;
	  color: #FFFFFF;
	  font-size: 30rpx;
	  position: fixed;
	  bottom:0rpx;
	  left:0rpx;
	  z-index:20;
	  border-radius: 0rpx!important;
  }
  .btns{
	  width:750rpx;
	  height: 98rpx;;
  }

</style>
